/* MEDIUM Geometria */
@font-face {
  font-family: "Geometria";
  font-style: normal;
  font-weight: 500;
  src: local(""), url("../fonts/geometria-medium.woff2") format("woff2"), url("../fonts/geometia-medium.woff") format("woff"); }

/* BOLD Geometria */
@font-face {
  font-family: "Geometria";
  font-style: normal;
  font-weight: 700;
  src: local(""), url("../fonts/geometria-bold.woff2") format("woff2"), url("../fonts/geometria-bold.woff") format("woff"); }

/* REGULAR Caveat */
@font-face {
  font-family: "Caveat", cursive;
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../fonts/caveat_brush.woff2") format("woff2"), url("../fonts/caveat_brush.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  background-color: red; }
  @media (min-width: 768px) {
    .header__top {
      display: flex;
      justify-content: space-between;
      width: 627px;
      margin: 0 auto; } }
  .header__phone {
    color: #ffffff; }
    @media (min-width: 399px) {
      .header__phone {
        width: 110px; } }
    @media (min-width: 768px) {
      .header__phone {
        width: 142px; } }
    @media (min-width: 999px) {
      .header__phone {
        width: 186px; } }
  .header__logo {
    width: 89px;
    height: 29px; }
    @media (min-width: 768px) {
      .header__logo {
        width: 120px; } }
    @media (min-width: 999px) {
      .header__logo {
        width: 152px; } }
  .header__img {
    width: 89px;
    height: 29px; }
    @media (min-width: 768px) {
      .header__img {
        width: 120px;
        height: 39px; } }
    @media (min-width: 999px) {
      .header__img {
        width: 152px;
        height: 49px; } }

@media (min-width: 768px) {
  .main-nav {
    width: 345px; } }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none; }

@media (min-width: 768px) {
  .main-nav__toogle {
    display: none; } }

.site-list__link {
  color: #ffffff; }
